@import "../global/colors";
@import "../global/variables";

.hero {
  padding-top: 90px;
  padding-bottom: 85px;
  text-align: center;
  background-image: url("../img/divider.svg"), url("../img/index/hero_banner_1200x518.jpg");
  background-repeat: no-repeat;
  background-position: 50% 100%, 50% 25%;
  background-size: 172%, 285%;
  background-color: rgba(131, 179, 211, 0.5);

  &--catalog {
    padding-top: 53px;
    padding-bottom: 67px;
    background-position: 50% 100%, 50% 100%;
  }

  &--form {
    padding-top: 52px;
    padding-bottom: 68px;
    background-position: 50% 100%, 50% 100%;
  }
}
